import { Grid, makeStyles } from "@material-ui/core";
import axios from "axios";
import React from "react";
const useStyles = makeStyles((theme) => ({
  fullWidth: {
    width: "100%",
  },
}));
const ContactFooter = () => {
  const classes = useStyles();
  const sendData = (event) => {
    event.preventDefault();
    console.log("Running");
    const form = document.forms['contact-form']
    axios({
      method: "post",
      url: "https://script.google.com/macros/s/AKfycbxArX0fbEzto0E4B1WawLFC2XvfNSHFzV7Io_nsGjA1LK3DJSiyDzX4imMEunbbTh3b/exec",
      data: new FormData(form),
      headers: { "Content-Type": "multipart/form-data" },
    })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (response) {
        console.log(response);
      });
    form.reset()
  };
  return (
    <>
      <Grid item xs={12} md={4} className="">
        <div className="small my-2">Or just write me a letter here_</div>
        <form
          className="footer-contact-form"
          onSubmit={sendData}
          name="contact-form"
        >
          <input
            type="text"
            placeholder="Your Name"
            name="name"
            className={`${classes.fullWidth} my-1`}
          />
          <input
            type="email"
            placeholder="Your e-mail"
            name="email"
            className={`${classes.fullWidth} my-1`}
          />
          <textarea
            placeholder="Type the message here"
            name="message"
            className={`${classes.fullWidth} my-1`}
          />
          <button type="submit" className={`${classes.fullWidth} my-1 p-2`}>
            Send
          </button>
        </form>
      </Grid>
    </>
  );
};
export default ContactFooter;
